import { Component, OnInit, OnDestroy } from '@angular/core'
import { CommonModule } from '@angular/common'
import { ActivatedRoute, Router, RouterLink } from '@angular/router'
import {
  IonContent,
  IonSpinner,
  IonButton,
  IonIcon,
  IonGrid,
  IonRow,
  IonCol,
  IonSkeletonText,
} from '@ionic/angular/standalone'
import { FillButtonComponent } from 'src/app/components/fill-button/fill-button.component'
import { VehiculesService } from 'src/api-cyclist'
import {
  ListVehicule200ResponseAllOfDataInner,
  ListTrip200ResponseAllOfDataInner,
} from 'src/api-cyclist'
import { environment } from 'src/environments/environment'
import { firstValueFrom } from 'rxjs'
import { RentingService } from 'src/app/services/api-wrappers/renting.service'
import { TranslateModule, TranslateService } from '@ngx-translate/core'
import { ban, batteryFull, batteryHalf, batteryDead } from 'ionicons/icons'
import { addIcons } from 'ionicons'

export type QrCodePageState =
  | { state: 'loading_program' } // Universal link: loading program + colors
  | { state: 'loading_vehicle' } // App opened: loading vehicle details
  | { state: 'vehicle_loaded'; vehicle: ListVehicule200ResponseAllOfDataInner }
  | {
      state: 'vehicle_not_rentable'
      vehicle: ListVehicule200ResponseAllOfDataInner
    }
  | { state: 'unlocking'; vehicle: ListVehicule200ResponseAllOfDataInner }
  | { state: 'success'; trip: ListTrip200ResponseAllOfDataInner }
  | { state: 'error'; error: string }

@Component({
  selector: 'app-qr-code',
  templateUrl: './qr-code.page.html',
  styleUrls: ['./qr-code.page.scss'],
  standalone: true,
  imports: [
    IonContent,
    IonSpinner,
    IonButton,
    IonIcon,
    IonGrid,
    IonRow,
    IonCol,
    IonSkeletonText,
    CommonModule,
    TranslateModule,
    FillButtonComponent,
    RouterLink,
  ],
})
export class QrCodePage implements OnInit, OnDestroy {
  private vehicleID: string | null = null
  pageState: QrCodePageState = { state: 'loading_program' }
  private autoUnlockTimer?: number

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private vehiclesService: VehiculesService,
    private rentingService: RentingService,
    private translate: TranslateService
  ) {
    addIcons({
      ban,
      batteryFull,
      batteryHalf,
      batteryDead,
    })
  }

  async ngOnInit() {
    // Get qrcode from query params first
    this.route.queryParams.subscribe(async (params) => {
      this.vehicleID = params['qrcode']
      if (!this.vehicleID) {
        this.onError(this.translate.instant('qr_code.error.missing_vehicle_id'))
        return
      }
      await this.loadVehicleDetails()
    })
  }

  ngOnDestroy() {
    this.resetState()
  }

  private async loadVehicleDetails(): Promise<void> {
    this.pageState = { state: 'loading_vehicle' }
    if (!this.vehicleID) {
      throw new Error('No vehicleID available')
    }

    try {
      const vehicle = await firstValueFrom(
        this.vehiclesService.retrieveVehicule({
          program: environment.program,
          id: this.vehicleID,
          liveMode: environment.production,
        })
      )

      // Ensure vehicle has minimum required data
      if (!vehicle || !vehicle.id) {
        throw new Error('Vehicle data incomplete')
      }

      // Check if vehicle is rentable
      if (!vehicle.rentable) {
        this.pageState = {
          state: 'vehicle_not_rentable',
          vehicle,
        }
        return
      }

      // Si batterie > 40%, on déverrouille directement
      const batteryPercent = vehicle.battery_vae?.percent

      if (batteryPercent && batteryPercent > 40) {
        await this.startRenting(vehicle)
      } else {
        this.pageState = { state: 'vehicle_loaded', vehicle }
        this.startAutoUnlockTimer()
      }
    } catch (error: any) {
      this.onError(
        this.translate.instant('qr_code.error.' + error.error.error) ||
          `Impossible de trouver le véhicule n° ${this.vehicleID}`
      )
    }
  }

  async confirmUnlock(): Promise<void> {
    if (this.pageState.state !== 'vehicle_loaded' || !this.pageState.vehicle)
      return

    // Clear the auto-unlock timer if user clicks manually
    this.clearAutoUnlockTimer()

    await this.startRenting(this.pageState.vehicle)
  }

  resetState(): void {
    this.clearAutoUnlockTimer()
    this.pageState = { state: 'loading_program' }
  }

  private async startRenting(
    vehicle: ListVehicule200ResponseAllOfDataInner
  ): Promise<void> {
    try {
      this.pageState = { state: 'unlocking', vehicle }

      // Start renting with auto-unlock
      const trip = await this.rentingService.startRenting(
        vehicle,
        false, // checkLocation
        vehicle.boitier_model === 'bill' ? true : false, // autoUnlock
        false, // showLoader
        vehicle.boitier_model === 'bill' ? true : false // skipRedirect
      )

      vehicle.boitier_model === 'bill' &&
        this.onSuccess(trip as ListTrip200ResponseAllOfDataInner)
    } catch (error: any) {
      console.error('❌ Error during unlock:', error)
      const errorMessage =
        error.error?.message?.split('|')[1]?.trim() ||
        error.error?.message ||
        `Impossible de déverrouiller le vélo ${vehicle?.number || ''}`
      this.onError(errorMessage)
    }
  }

  private startAutoUnlockTimer(): void {
    // Clear any existing timer
    this.clearAutoUnlockTimer()

    // Start 5-second timer to auto-unlock
    this.autoUnlockTimer = window.setTimeout(async () => {
      if (this.pageState.state === 'vehicle_loaded' && this.pageState.vehicle) {
        await this.startRenting(this.pageState.vehicle)
      }
    }, 5000)
  }

  private clearAutoUnlockTimer(): void {
    if (this.autoUnlockTimer) {
      clearTimeout(this.autoUnlockTimer)
      this.autoUnlockTimer = undefined
    }
  }

  private onError(error: string): void {
    this.pageState = { state: 'error', error: error }
    // Auto redirect to map after 5 seconds on error
    setTimeout(() => {
      this.resetState()
      this.router.navigate(['/map'], { replaceUrl: true })
    }, 5000)
  }
  private onSuccess(trip: ListTrip200ResponseAllOfDataInner): void {
    this.pageState = { state: 'success', trip }
    // Auto redirect to map after 6 seconds on success
    setTimeout(() => {
      this.resetState()
      this.router.navigate(['/map'], { replaceUrl: true })
    }, 6000)
  }
}
